import React, { Component, Fragment } from 'react';
import { CustomInput, Input, Label, FormGroup, Row, Col, Button, Badge } from 'reactstrap';
import { JOFGENGenerator, isValidDesignJSON, isValidValueJSON } from './../../lib';
import './../../lib/css/generator.css';
import Icon from '@mdi/react';
import { local_urls } from '../../urls';

export default class Playground_Generator extends Component {
    constructor(props) {
        super(props);
        this.state = {
            fatal_error_msg: "Ooops... Something went wrong. Unable to render form.",
            jsonObj: [],
            json: "",
            jsonis_valid: true,
            valueObj: [],
            value: "",
            mode: "linear",
            size: "sm",
            sep: ":",      
            sm: "12",        
            req_indicator: "*",       
            is_valid: true
        };
    }

    onChange = function (e) {
        switch (e.target.name) {
            case 'json':
                var jsonObj;
                var jsonis_valid;
                try {
                    jsonObj = JSON.parse(e.target.value);
                    jsonis_valid = isValidDesignJSON(jsonObj,this.state.mode)
                }
                catch
                {
                    jsonObj = e.target.value;
                    jsonis_valid = false;
                }

                this.setState({
                    json: e.target.value,
                    jsonObj: jsonObj,                    
                    jsonis_valid: jsonis_valid
                });
                break;

            case 'value':
                var valueObj;
                var valueValid;
                try {
                    valueObj = JSON.parse(e.target.value);
                    valueValid = isValidValueJSON(this.state.jsonObj, valueObj, this.state.mode, this.state.sep);
                }
                catch
                {
                    valueObj = e.target.value;
                    valueValid = false;
                }

                this.setState({
                    value: e.target.value,
                    valueObj: valueObj,
                    is_valid: valueValid
                });
                break;

            default:
                var obj = {};
                obj[e.target.name] = e.target.value;
                this.setState(obj);
        }
    }.bind(this)

    onChangeGen = function (value, valid) {
        this.setState({
            valueObj: value,
            is_valid: valid,
            value: JSON.stringify(value)
        })
    }.bind(this)

    onClear = function () {
        this.setState({
            fatal_error_msg: "Ooops... Something went wrong. Unable to render form.",
            jsonObj: [],
            json: "",
            jsonis_valid: true,
            valueObj: [],
            value: "",
            is_valid: true,
            mode: "linear",
            size: "sm",
            sep: ":",
            sm: "12",
            req_indicator: "*"
        });
    }.bind(this)

    onChangeBool = function (e) {
        var state = {};
        state[e.target.name] = e.target.checked;
        this.setState(state);
    }.bind(this)

    render() {
        return (
            <Fragment>
                <section>
                    <div className="mt-3">
                        <Row>
                            <Col className="mx-300">
                                <div className="p-4 card filter">
                                    <div className="title">Properties</div>
                                    <FormGroup className="pt-1">
                                        <Label>fatal_error.text</Label>
                                        <Input type="text" bsSize="sm" name="fatal_error_msg" value={this.state.fatal_error_msg} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>json</Label>
                                        <div style={{ float:'right' }}>
                                            {(this.state.jsonis_valid || this.state.json.length === 0)?<Badge color="success">Valid</Badge>:<Badge color="danger">Invalid</Badge>}
                                        </div>
                                        <Input type="textarea" bsSize="sm" name="json" value={this.state.json} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>mode</Label>
                                        <CustomInput type="select" bsSize="sm" name="mode" value={this.state.mode} onChange={this.onChange}>
                                            <option value="linear">linear</option>
                                            <option value="linear_merge">linear_merge</option>
                                            <option value="tree">tree</option>
                                        </CustomInput>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>size</Label>
                                        <CustomInput type="select" bsSize="sm" name="size" value={this.state.size} onChange={this.onChange}>
                                            <option value="sm">small</option>
                                            <option value="lg">large</option>
                                        </CustomInput>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>req_indicator</Label>
                                        <Input type="text" bsSize="sm" name="req_indicator" value={this.state.req_indicator} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>sep</Label>
                                        <Input type="text" bsSize="sm" name="sep" value={this.state.sep} onChange={this.onChange} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>sm</Label>
                                        <CustomInput type="select" bsSize="sm" name="sm" value={this.state.sm} onChange={this.onChange}>
                                            <option value="1">1</option>
                                            <option value="2">2</option>
                                            <option value="3">3</option>
                                            <option value="4">4</option>
                                            <option value="5">5</option>
                                            <option value="6">6</option>
                                            <option value="7">7</option>
                                            <option value="8">8</option>
                                            <option value="9">9</option>
                                            <option value="10">10</option>
                                            <option value="11">11</option>
                                            <option value="12">12</option>
                                        </CustomInput>
                                    </FormGroup>
                                    <FormGroup>
                                        <Label>value</Label>
                                        <div style={{ float:'right' }}>
                                            {(this.state.is_valid || this.state.value.length === 0)?<Badge color="success">Valid</Badge>:<Badge color="danger">Invalid</Badge>}
                                        </div>
                                        <Input type="textarea" bsSize="sm" name="value" value={this.state.value} onChange={this.onChange} />
                                    </FormGroup>
                                    <div className="mt-3 btns text-right">
                                        <Button color="link" size="sm" onClick={this.onClear} >Clear</Button>
                                    </div>
                                </div>
                            </Col>
                            <Col>
                                <div className="p-2">
                                    <JOFGENGenerator
                                        sep={(this.state.sep)?this.state.sep:":"}
                                        /*sm={(this.state.sm)?this.state.sm:"12"}*/
                                        size={this.state.size}
                                        mode={this.state.mode}
                                        fatal_error={
                                            {
                                                icon: <svg className="jofgen-icon jofgen-icon-fatal-error" viewBox='2 2 25 25'><path fill='#ced4da' d='M22.1 21.5L2.4 1.7L1.1 3L7.9 9.8L3.5 17.4C3.2 17.8 3 18.4 3 19C3 20.7 4.3 22 6 22H18C18.6 22 19.2 21.8 19.6 21.5L20.8 22.7L22.1 21.5M18 20H6C5.4 20 5 19.6 5 19C5 18.8 5.1 18.6 5.2 18.4L9.4 11.2L13.6 15.4L13 16L10.4 13.4L7.7 18H16.1L18 20C18.1 20 18 20 18 20M11 7.8L8 4.8V4C8 2.9 8.9 2 10 2H14C15.1 2 16 2.9 16 4V5C16 5.6 15.6 6 15 6V7.8L20.4 17.2L15 11.8L13 8.4V4H11V7.8Z' /></svg>,
                                                text: (this.state.fatal_error_msg) ? this.state.fatal_error_msg : "Ooops... Something went wrong. Unable to render form."
                                            }
                                        }
                                        json={(this.state.json) ? this.state.jsonObj : []}
                                        value={(this.state.value) ? this.state.valueObj : {}}
                                        
                                        onChange={this.onChangeGen} />
                                </div>
                            </Col>
                        </Row>
                    </div>
                </section>
            </Fragment>
        );

    }
}